import React from "react";
import "./Project.css";
import ReactHtmlParser from "react-html-parser";
import AOS from "aos";
import "aos/dist/aos.css";

class Project extends React.Component {
  state = { list: "", presentation: "", mouseover: 1, style: "", clicked: 0 };

  updateSize() {
    document.getElementById(`pres${this.props.id}`).style.display = "flex";
    document.getElementById(`desc${this.props.id}`).style.display = "none";
  }

  componentDidMount() {
    window.addEventListener("resize", this.updateSize.bind(this));
    if (this.props.demo === undefined) {
      this.setState({ style: "none" });
    }
    try {
      let temp = "<div class='div-ul'>";
      this.props.technology.map(element => {
        temp += `<b class='div-li${this.props.type}'>${element}</b>`;
        return 0;
      });
      temp += "</div>";
      this.setState({ list: temp });

      if (this.props.presentation === "iframe") {
        temp = `<iframe
            id=${this.props.alt}
            type="text/html"
            width="100%"
            height="100%"
            src=${this.props.src}
            allowfullscreen="allowfullscreen"
            mozallowfullscreen="mozallowfullscreen" 
            msallowfullscreen="msallowfullscreen" 
            oallowfullscreen="oallowfullscreen" 
            webkitallowfullscreen="webkitallowfullscreen"
            frameborder="0"
          />`;

        this.setState({ presentation: temp });
      }
      if (this.props.presentation === "image") {
        temp = `<img
      src=${this.props.src}
      id=${this.props.id}
      alt=${this.props.alt}
    />`;

        this.setState({ presentation: temp });
      }
    } catch (err) {}
  }

  onMouseOver = e => {
    if (window.innerWidth > 900) {
      document.getElementById(`pres${this.props.id}`).style.display = "none";
      document.getElementById(`desc${this.props.id}`).style.display = "block";
    }
  };

  onClick = e => {
    if (window.innerWidth <= 900) {
      if (this.state.clicked === 0) {
        document.getElementById(`pres${this.props.id}`).style.display = "none";
        document.getElementById(`desc${this.props.id}`).style.display = "block";
        this.setState({ clicked: 1 });
      }
      if (this.state.clicked === 1) {
        document.getElementById(`pres${this.props.id}`).style.display = "flex";
        document.getElementById(`desc${this.props.id}`).style.display = "none";
        this.setState({ clicked: 0 });
      }
    }
  };

  onMouseOut = () => {
    if (window.innerWidth > 900) {
      document.getElementById(`pres${this.props.id}`).style.display = "flex";
      document.getElementById(`desc${this.props.id}`).style.display = "none";
    }
  };

  render() {
    AOS.init();
    return (
      <div
        className="project"
        onMouseOver={this.onMouseOver}
        onMouseOut={this.onMouseOut}
        onClick={this.onClick}
      >
        <div className="presentation" id={"pres" + this.props.id}>
          {ReactHtmlParser(this.state.presentation)}
        </div>
        <div
          className={`description ${"t-" + this.props.type}`}
          id={"desc" + this.props.id}
        >
          <div className={`pro-title ${"tit-" + this.props.type}`}>{this.props.title}</div>
          <div className="pro-desc">{this.props.description}</div>
          {ReactHtmlParser(this.state.list)}
          <div className="links">
            <a href={this.props.href}>
              <div className={`link ${"l-" + this.props.type}`}>Github</div>
            </a>
            <a href={this.props.demo}>
              <div
                className={`link ${this.state.style} ${"l-" + this.props.type}`}
              >
                Demo
              </div>
            </a>
          </div>
        </div>
      </div>
    );
  }
}

export default Project;
