import React from "react";
import "./About.css";
import AOS from "aos";
import "aos/dist/aos.css";

class About extends React.Component {
  componentDidMount() {
    AOS.init();
  }

  render() {
    return (
      <div className="about" id="A2">
        <div id="box-1" className="box">
          <div id="about" className="about-photo">
            <img src="me.jpg" id="me" alt="me"></img>
          </div>

          <div id="life" className="desc">
            <div className="desc-text">
              <p>
                <b>O mnie :</b>
              </p>
              Mam 22 lata. Ukończyłem kierunek licencjacki: <b>informatyka</b>{" "}
              na wydziale MFI na Uniwesytecie Gdańskim ze specjalizacją:{" "}
              <b>aplikacje internetowe i bazy danych</b>. Interesuję się naprawą
              urządzeń elektronicznych , jazdą wyczynową na rolkach, jazdą
              wyczynową na łyżwach, sportem oraz słuchaniem muzyki. Dodatkową
              przyjemność sprawia mi tworzenie aplikacji internetowych.
            </div>
          </div>
        </div>

        <div id="box-2" className="box">
          <div id="skills" className="desc">
            <p>
              <b>Umiejętności :</b>
            </p>
            <ul>
              <li>
                <b>Javascript</b>, wraz z Dom Api, Jquery oraz Ajax.
              </li>
              <li>ReactJS, wraz z Redux oraz Vue, wraz z Vuex.</li>
              <li>Html, CSS, Less, Sass.</li>
              <li>ExpressJS, pisanie własnych serwerów.</li>
              <li>Znajomość systemu Linux oraz Git.</li>
              <li>Biegła obsługa programów: Excel oraz Word.</li>
              <li>
                Dodatkowo podczas studiowania miałem kontakt z językami: C,
                Java, C++, Ruby, Assembler, C#, batch, SQL, PostgreSQL.
              </li>
            </ul>
          </div>
          <div id="about" className="about-photo">
            <img src="me1.jpg" id="me1" alt="me1"></img>
          </div>
        </div>

        <div id="box-3" className="box">
          <div id="about" className="about-photo">
            <img src="me2.jpg" id="me2" alt="me2"></img>
          </div>

          <div className="desc">
            <p>
              <b>UWAGA!: </b>
            </p>
            <p>
              Poniżej znajdują się wybrane przeze mnie projekty wraz z
              przekierowaniami do ich repozytoriów. Tworząc moje prywatne
              projekty staram się używać własnych umiejętności CSS/LESS/SASS.
            </p>
          </div>
        </div>
      </div>
    );
  }
}

export default About;
